
body {
  margin: 0px;
  margin-top: 120px;
  padding: 0px;
}
#app {
  font-family: "Verdana";
  width: 600px;
  margin: 0 auto;
}
label {
/*   display: grid;
  text-align: right;
  grid-template-columns: 80px auto;
  grid-gap: 10px;
  width: 300px;
  margin-bottom: 10px; */
}
canvas {
  position: absolute;
  top: 0px;
  left: 0;
}
input[type=button] {
  margin-right: 5px;
  padding: 5px 10px;
}
.controls {
  display: grid;
  width: 300px;
  grid-gap: 10px;
  margin: 0 auto;
}
.controls > p {
  margin: 0px;
}
.config {
  width: 600px;
  margin: 0 auto;
  margin-top: 20px;
  padding: 10px;
  //border: 1px solid black;
}
.font-config {
  margin: 20px 0px;
}
.font-config > label {
  display: grid;
  text-align: right;
  grid-template-columns: 120px -webkit-max-content;
  grid-template-columns: 120px max-content;
  grid-gap: 10px;
  width: 200px;
  margin-bottom: 10px;
}
.color-config {
  display: grid;
  grid-template-columns: 250px 250px;
  grid-gap: 10px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.info {
  width: 600px;
  margin: 0 auto;
}
.vc-chrome {
  display:inline-block;
}
